import React, { useEffect, useState } from 'react'
import {useTranslation} from "react-i18next"
import { TRANSLATE_NS } from 'src/constants'
import useIsMountedRef from 'src/hooks/useIsMountedRef'
import setsService from 'src/services/setsService'
import Page from 'src/components/Page'
import SplashScreen from 'src/components/SplashScreen'
import {
  Box,
  Container,
  Divider,
  Grid,
  makeStyles,
  Paper,
  Typography
} from '@material-ui/core'
import { v4 as uuidv4 } from 'uuid'
import useWindowSize from 'src/hooks/useWindowSize'
import PerfectScrollbar from 'react-perfect-scrollbar'
import Equipment from './Equipment'
import Header from './Header'
import { useHistory } from 'react-router-dom'

const useStyles = makeStyles((theme)=> ({
  root: {
    backgroundColor: theme.palette.background.default,
    minHeight: '100%',
    padding: theme.spacing(2, 0),
  },
  wrapper: {
    height: 'inherit'
  },
  info: {
    
  },
  items: {
    height: '80%',
    width: 'auto'
  },
  amountDetails: {
    display: 'flex',
    justifyContent: 'flex-end',
    alignItems: 'center'
  },
  amount: {
    fontSize: theme.typography.h2.fontSize,
    color: theme.palette.text.primary,
    marginLeft: theme.spacing(1),
    display: 'inline',
    fontWeight: 500
  }
}))

function DetailsView({match}){
  const classes = useStyles()
  const {t} = useTranslation([TRANSLATE_NS.SETS, TRANSLATE_NS.TRANSLATIONS])
  const isMountedRef = useIsMountedRef()
  const [dataItem, setDataItem] = useState(null)
  const history = useHistory()
  const size = useWindowSize()  
  const setId = match.params?.id

  useEffect(()=> {
    
    async function getById() {
      try{
        const response = await setsService.getById(setId)
        console.log('response: ', response)
        if(response.resultFlag){
          const dataItem = response.value
          if(isMountedRef.current){
            setDataItem(()=> {
              return {...dataItem}
            })
          }
        }
      }
      catch(error){
        console.error(error)
      }
    }

    if(setId){
      getById()
    }

  }, [setId, isMountedRef, history])

  if(!dataItem){
    return <SplashScreen />
  }

  const name = dataItem?.name ?? ""
  const active = dataItem?.active 
  const equipments = dataItem?.equipments

  return (
    <Page
      className={classes.root}
      title={t("sets:surgical type details")}
    >
      <Container maxWidth="lg">
        <Header 
          mb={1} 
          display="flex"
          name={name} 
          active={active}
        />
        <Box height={size.height - 90}>
          <Paper
            className={classes.wrapper}
            elevation={0}
            variant="outlined"
          >
            <Box 
              m={2} 
              className={classes.info}
            >
              <Typography 
                variant="body2" 
                component="div" 
                className={classes.amountDetails}
                gutterBottom
              >
                <Box 
                  fontWeight="fontWeightLight" 
                  component="p" 
                  display="inline"
                >
                  {t('sets:form2.equipment type')}
                </Box>
                <Box 
                  component="p" 
                  className={classes.amount}
                >
                  {equipments?.totalItems}{` ${t('sets:form2.piece')}`}
                </Box>
                <Box 
                  ml={4} 
                  fontWeight="fontWeightLight" 
                  component="p" 
                  display="inline"
                >
                  {t('sets:form2.tool total')}
                </Box>
                <Box 
                  component="p" 
                  className={classes.amount}
                >
                  {equipments?.totalEquipments}{` ${t('sets:form2.type')}`}
                </Box>
              </Typography>
            </Box>            
            <Divider />
            <Box 
              m={2} 
              className={classes.items}
            >
              <PerfectScrollbar>
                <Grid container>
                  {equipments && equipments.allIds && equipments.allIds.map((equipmentId)=> {
                    const id = uuidv4()
                    const equipment = equipments.byId[equipmentId]
                    return (
                      <Grid
                        key={id} 
                        item
                        xs={12}
                        md={4}
                        lg={4}
                      >
                        <Equipment equipment={equipment} />
                      </Grid>
                    )
                  })}
                  {(!equipments || !equipments.allIds || !equipments.allIds.length) && (
                    <Typography>
                      {t("translations:no data available on select items")}
                    </Typography>
                  )}
                </Grid>
              </PerfectScrollbar>
            </Box>
          </Paper>
        </Box>
      </Container>
    </Page>
  )
}

export default DetailsView