import axios from 'src/utils/axios'
import { initFilter } from 'src/utils/filters'
import * as handleResponse from './response'

class SetsService {

  getAll = () => new Promise((resolve, reject) => {
    axios.get('/api/surgeries/types')
      .then((response)=> {
        const result = handleResponse.accessResponse(response)
        resolve(result)
      })
      .catch((error) => {
        const result =  handleResponse.accessError(error)
        reject(result)
      })
  })  

  getById = (setId) => new Promise((resolve, reject) => {    
    axios
      .get(`/api/surgeries/types/set/${setId}`, {
        params: { id: setId }
      })
      .then((response)=> {
        const result = handleResponse.accessResponse(response)
        if(result.value && result.value.equipments){

          const allIds = Object.keys(result.value.equipments.byId)
          result.value.equipments["allIds"] = allIds
          resolve(result)
        }
        else {
          resolve(result)
        }
      })
      .catch((error) => {
        const result =  handleResponse.accessError(error)
        reject(result)
      })
  })

  getList = (filter, sort, page, pageSize) => new Promise((resolve, reject) => {
    const customFilter = filter && filter !== "" 
      ? initFilter({ field: "name", value: filter })
      : ""
    
    axios.get('/api/surgeries/types', { params: { 
      filter: customFilter, 
      sort, 
      page, 
      pageSize } 
    })
      .then((response)=> {
        const result = handleResponse.accessResponse(response)
        resolve(result)
      })
      .catch((error) => {
        const result =  handleResponse.accessError(error)
        reject(result)
      })
  })

  insert = (dataItem) => new Promise((resolve, reject) => {
    console.log('insert: ', dataItem)
    axios.post('/api/surgeries/types', dataItem)
      .then((response)=> {
        const result = handleResponse.accessResponse(response)
        resolve(result)
      })
      .catch((error) => {
        const result =  handleResponse.accessError(error)
        reject(result)
      })
  })

  update = (dataItem) => new Promise((resolve, reject) => {
    axios.put('/api/surgeries/types', dataItem) 
      .then((response)=> {
        const result = handleResponse.accessResponse(response)
        resolve(result)
      })
      .catch((error) => {
        const result =  handleResponse.accessError(error)
        reject(result)
      })
  })

  remove = (dataItem) => new Promise((resolve, reject) => {

    console.log('remove: ', dataItem)

    axios.delete(`/api/surgeries/types/${dataItem.id}`, {
      ...dataItem
    })
      .then((response)=> {
        console.log(response)
        const result = handleResponse.accessResponse(response)
        resolve(result)
      })
      .catch((error) => {
        const result =  handleResponse.accessError(error)
        reject(result)
      })
  })

  updateEquipmentItems = ({ setId, equipmentItems, lastModifiedTimestamp }) => new Promise((resolve, reject) => {
    
    axios.put(`/api/surgeries/types/set/${setId}`, {
      id: setId, 
      lastModifiedTimestamp: lastModifiedTimestamp,
      equipments: equipmentItems /* { equipmentId: amount } */
    })
      .then((response)=> {
        const result = handleResponse.accessResponse(response)
        resolve(result)
      })
      .catch((error) => {
        const result =  handleResponse.accessError(error)
        reject(result)
      })
  })
}

const setsService = new SetsService()

export default setsService