import React from 'react'
import PropTypes from 'prop-types'
import clsx from 'clsx'
import { useTranslation } from 'react-i18next'
import { TRANSLATE_NS } from 'src/constants'
import { Avatar, Box, makeStyles, Paper, Grid, Typography, Tooltip } from '@material-ui/core'

const useStyles = makeStyles((theme)=> ({
  root: { },
  paper: {
    padding: theme.spacing(1),
    maxWidth: 500,
    margin: theme.spacing(1)
  },
  img: {
    display: 'block',
    maxWidth: '100%',
    maxHeight: '100%',
  },
  rounded: {
    color: '#fff',
    backgroundColor: '#fff',
    width: 90,
    height: 90,
    marginRight: theme.spacing(1)
  },
  divName: {
    maxWidth: 205, 
    whiteSpace: 'normal'
  },
  pName: {    
    maxHeight: 60,
    textOverflow: 'ellipsis', 
    overflow: 'hidden', 
    backgroundColor: 'background.paper'
  },
  square: {
    margin: theme.spacing(0, 1, 0, 1)
  }
}))

function Equipment({equipment, className, ...rest}){
  const classes = useStyles()  
  const { t } = useTranslation([TRANSLATE_NS.TRANSLATIONS, TRANSLATE_NS.SETS])

  let displayFirstCharacter = "T"
  if(!equipment.media){
    displayFirstCharacter = equipment.name.charAt(0)
  }

  return (
  <div 
    className={clsx(classes.root, className)}
    {...rest}
  >
    <Paper 
      className={classes.paper} 
      variant="outlined"
    >
      <Grid container>
        <Grid item>
          {equipment.media ? (
            <Avatar
              variant="rounded"
              className={classes.rounded}
            >
              <img 
                className={classes.img} 
                alt="complex" 
                src={equipment.media} 
              />
            </Avatar>
          ) : (
            <Avatar variant="square" className={classes.square}>
              {displayFirstCharacter}
            </Avatar>
          )}
        </Grid>
        <Grid item xs={12} sm container>
          <Grid item xs container direction="column">
            <Grid item xs>
              <div className={classes.divName}>
                <Tooltip
                  title={equipment.name}
                >
                  <Typography variant="body2" 
                    my={2}
                    className={classes.pName}
                  >
                    {equipment.name}
                  </Typography>
                </Tooltip>
              </div>
            </Grid>
          </Grid>
          <Grid item>
            
            <Typography 
              variant="body2"
              component="div"
            >
              <Box 
                fontWeight="fontWeightLight" 
                component="p" 
                display="inline"
                fontSize="h2.fontSize"
              >
                {equipment.amount}
              </Box>              
              {` ${t('sets:form2.piece')}`}
            </Typography>
          </Grid>
        </Grid>
      </Grid>
    </Paper>
  </div>
  )
}

Equipment.prototypes = {
  equipment: PropTypes.shape({
    id: PropTypes.string,
    name: PropTypes.string,
    media: PropTypes.string,
    amount: PropTypes.number
  }),
  className: PropTypes.string
}

export default Equipment