import React from 'react'
import clsx from 'clsx'
import PropTypes from 'prop-types'
import { Link as RouterLink } from 'react-router-dom'
import { Box, Button, Typography, makeStyles } from '@material-ui/core'
import Label from 'src/components/Label'
import {useTranslation} from "react-i18next"
import { TRANSLATE_NS } from 'src/constants'

const useStyles = makeStyles((theme)=> ({
  root: {},  
  label: {
    marginLeft: theme.spacing(2)
  },
}))

function Header({name, active, className, ...rest}){
  const classes = useStyles()
  const {t} = useTranslation([TRANSLATE_NS.SETS, TRANSLATE_NS.TRANSLATIONS])

  return (
    <Box
      className={clsx(className, classes.root)}
      {...rest}
    >
      <Box 
        flexGrow={1}
      >
        <Typography 
          variant="body2" 
          component="div"
          color="textPrimary" 
          gutterBottom
        >
          <Box 
            fontWeight={500} 
            ml={1} 
            component="p" 
            display="inline"
            fontSize="h1.fontSize"
          >
            {name}
          </Box>
          {active && (
            <Label 
              className={classes.label}
              color={active === true? "success": "error"}
            >
              {active === true 
                ? t("translations:active") 
                : t("translations:inactive")}
            </Label>
          )}
        </Typography>
      </Box>          
      <Button
        variant="contained"
        size="medium"
        color="primary"
        component={RouterLink}
        to="/sets"
      >
        {t('sets:button.exit')}
      </Button>
    </Box>
  )
}

Header.propTypes = {
  className: PropTypes.string,
  name: PropTypes.string,
  active: PropTypes.bool
}

export default Header